import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import validationPindahBarang from "../../Validations/validationPindahBarang";
import { getListBarang } from "../../actions/kodeBarangAction";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormEditTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBarang: [],
      kodeJenis: "",
      kadarCetak: "",
      kadarBeli: "",
      bruto: 0,
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }

  handleChange = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarBeli: result_explode[2],
      kadarJual: result_explode[3],
      namaJenis: result_explode[4],
    });
  };

  setKadar() {
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
    this.props.change("kadarJual", this.state.kadarJual);
    this.props.change("keterangan", this.state.namaJenis);
  }
  setNetto() {
    this.props.change(
      "netto",
      this.props.netto === "NaN" ? 0 : this.props.netto
    );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 form-group">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="kodeBarang"
              component="select"
              className="form-control"
              onChange={this.handleChange()}
              onBlur={this.setKadar()}
            >
              <option value="DEFAULT|DEFAULT|DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>

              {this.props.KodeBarangList.map((list) => (
                <option
                  value={
                    list.kode_jenis +
                    "|" +
                    list.kadar_cetak +
                    "|" +
                    list.kadar_beli +
                    "|" +
                    list.kadar_jual +
                    "|" +
                    list.nama_jenis +
                    "|" +
                    list.kode_kategori
                  }
                  key={list.kode_jenis}
                >
                  {list.nama_jenis}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-md-4 form-group">
            <Field
              name="keterangan"
              label="Keterangan"
              component={renderField}
              type="text"
              defaultValue={""}
            />
          </div>
          <div className="col-md-5"></div>
          <div className="col-md-2 form-group">
            <Field
              name="kadar"
              label="Kadar (%)"
              component={renderField}
              type="text"
              defaultValue={""}
            />
          </div>
          <div className="col-md-2 form-group">
            <Field
              name="pkg"
              label="Pkg (Gr)"
              component={renderField}
              type="text"
              onBlur={this.setNetto()}
              defaultValue={""}
            />
          </div>
          <div className="col-md-2 form-group">
            <Field
              name="bruto"
              label="Bruto (Gr)"
              component={renderField}
              type="text"
              onBlur={this.setNetto()}
              value={this.state.bruto}
            />
          </div>
          <div className="col-md-2 form-group">
            <Field
              name="kadarTransaksi"
              label="Harga (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
              defaultValue={""}
            />
          </div>
          <div className="col-md-2 form-group">
            <Field
              name="kadarJual"
              label="Harga (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
              defaultValue={""}
            />
          </div>
          <div className="col-md-2 form-group">
            <Field
              name="netto"
              label="Netto (Gr)"
              component={renderField}
              type="text"
              defaultValue={""}
            />
          </div>
          <div className="col-md-4 form-group">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              name="ongkos"
              label="Ongkos (Rp)"
              component="input"
              type="telp"
              className="form-control"
              defaultValue={0}
              {...currencyMask}
            />
          </div>
          <div className="col-md-12 form-group">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormEditTambahBarang = reduxForm({
  form: "FormEditTambahBarang",
  enableReinitialize: true,
  validate: validationPindahBarang,
})(FormEditTambahBarang);
const selector = formValueSelector("FormEditTambahBarang"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi } = selector(state, "bruto", "kadarTransaksi");
  return {
    initialValues: {
      kodeBarang: state.provinsi.editPindahBarang.kodeJenis,
      pkg: state.provinsi.editPindahBarang.pkg,
      kadarTransaksi: state.provinsi.editPindahBarang.kadarTransaksi,
      kadar: state.provinsi.editPindahBarang.kadar,
      netto: state.provinsi.editPindahBarang.netto,
      bruto: state.provinsi.editPindahBarang.bruto,
      ongkos: state.provinsi.editPindahBarang.rp,
      keterangan: state.provinsi.editPindahBarang.keterangan,
    },
    KodeBarangList: state.provinsi.kodeBarang,
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
  };
})(FormEditTambahBarang);
