import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getLokasiPindah } from "../../actions/supplierAction";
import { getToday, SkeletonLoading } from "../../getDate";
import { renderField, renderFieldSelect } from "../../renderField";
import validasiHeadPindahBarang from "../../Validations/validasiHeadPindahBarang";

class HeadPindahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      supplier: false,
      listKategori: [
        {
          nama: "AMBIL BARANG",
        },
        {
          nama: "KEMBALI STOK",
        },
        {
          nama: "KOREKSI STOK",
        },
        {
          nama: "MUTASI BAHAN",
        },
        {
          nama: "PENGAMBILAN SALES",
        },
        {
          nama: "PINDAH",
        },
        {
          nama: "PINDAH GUDANG REPARASI",
        },
        {
          nama: "RETURN SALES",
        },
        {
          nama: "SALAH INPUT",
        },
        {
          nama: "SALAH PINDAH",
        },
        {
          nama: "STOK AWAL",
        },
        {
          nama: "TAMBAH BARANG",
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getLokasiPindah());
    this.props.change("tanggal", getToday);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 form-group">
            <Field
              component={renderField}
              type="date"
              name="tanggal"
              label="Tanggal"
            />
          </div>
          <div className="col-md-4 form-group">
            {this.props.dataLokasiPindah ? (
              <Field
                component={renderFieldSelect}
                label="Dari"
                name="dari"
                value={this.props.status}
                normalize={(value) => value && value.toUpperCase()}
                options={this.props.dataLokasiPindah.map((obj) => {
                  var payload = {
                    value: obj.kode_lokasi,
                    name: obj.nama_lokasi,
                  };
                  return payload;
                })}
              ></Field>
            ) : (
              <SkeletonLoading label="Dari" />
            )}
          </div>
          <div className="col-md-1 text-center">
            <label htmlFor="" className="text-white">
              .
            </label>
            <h3>
              <i className="fa fa-exchange-alt"></i>
            </h3>
          </div>
          <div className="col-md-4 form-group">
            {this.props.dataLokasiPindah ? (
              <Field
                component={renderFieldSelect}
                label="Ke"
                name="ke"
                value={this.props.status}
                normalize={(value) => value && value.toUpperCase()}
                options={this.props.dataLokasiPindah.map((obj) => {
                  var payload = {
                    value: obj.kode_lokasi,
                    name: obj.nama_lokasi,
                  };
                  return payload;
                })}
              ></Field>
            ) : (
              <SkeletonLoading label="Ke" />
            )}
          </div>
          <div className="col-md-4 form-group">
            {this.props.dataLokasiPindah ? (
              <Field
                component={renderFieldSelect}
                label="Kategori"
                name="Kategori"
                value={this.props.status}
                normalize={(value) => value && value.toUpperCase()}
                options={this.state.listKategori.map((obj) => {
                  var payload = {
                    value: obj.nama,
                    name: obj.nama,
                  };
                  return payload;
                })}
              ></Field>
            ) : (
              <SkeletonLoading label="Kategori" />
            )}
          </div>
          <div className="col-md-4 form-group">
            <Field
              component={renderField}
              type="text"
              name="noBon"
              label="Bon Pindah"
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-lg-4"></div>
        </div>
        <button
          className="btn btn-primary"
          data-toggle="modal"
          disabled={this.props.onSend}
          onClick={this.props.handleSubmit}
        >
          {this.props.onSend ? (
            <span
              className="spinner-border spinner-border-sm mr-3"
              aria-hidden="true"
            ></span>
          ) : null}
          SIMPAN
          <i className="fa fa-paper-plane ml-3"></i>
        </button>
      </form>
    );
  }
}
HeadPindahBarang = reduxForm({
  form: "headPindahBarang",
  enableReinitialize: true,
  validate: validasiHeadPindahBarang,
})(HeadPindahBarang);
export default connect((state) => {
  return {
    dataLokasiPindah: state.provinsi.dataLokasiPindah,
  };
})(HeadPindahBarang);
